import React from 'react'
import Link from 'gatsby-link'

import Skillsection from '../../components/Skillsection'
import FirstSection from '../../components/FirstSection'
import Section from '../../components/Section'

import image from '../../../static/foto-av.jpg'

const IndexPage = ({ data }) => (
  <article>
    <header>
      <h1>
        <Link to="/">{data.content.edges[0].node.frontmatter.title}</Link>
      </h1>
      <img className="portrait" src={image} alt="portrait of alex jenter" />
      
      {data.person.edges.map(x => <FirstSection key={x.node.id} {...x.node} />)}
    </header>
    <main dangerouslySetInnerHTML={{ __html: data.content.edges[0].node.html}}>
    {/* <pre>
      {JSON.stringify(data.content.edges[0].node.html , null, 2)}
      
      </pre> */}
    </main>
  </article>
)

export const query = graphql`
  query LetterQuery {
    person: allMarkdownRemark(
      filter: { frontmatter: { title: { eq: "Person" } } }
    ) {
      edges {
        node {
          id
          frontmatter {
            position
            title
            links {
              href
              text
            }
          }
          html
        }
      }
    }
    content: allMarkdownRemark(
      filter: { frontmatter: { title: { eq: "Bewerbung" } } }
    ) {
      edges {
        node {
          id
          frontmatter {
            position
            title
            links {
              href
              text
            }
          }
          html
        }
      }
  }
  }
`

export default IndexPage



